.carousal {
  background-color: white;
  width: 100%;
  /* height: 60vh; */
  margin: 1rem 0;
  overflow: hidden;
}
.multi-slider-con {
  background: white;
  /* height: 40vh; */
  margin-bottom: 1rem;
  padding: 1rem;
}
.multi-slider-con h3 {
  padding: 1rem;
  padding-top: 0.5rem;
  color: rgb(27, 27, 31);
}
.multi-carousal-item {
  height: 250px;
  /* width: 20vh; */
}

.multi-carousal-item img {
  width: 100%;
  height: 80%;
  object-fit: cover;
}
.multi-carousal-item .name {
}

.multi-carousal-item .price {
  font-weight: bold;
}

.multi-carousal-item .price .cutted-price {
  text-decoration: line-through;
  color: red;
  margin-left: 12px;
  font-size: 0.7rem;
}
