* {
  margin: 0;
  padding: 0;
  color: rgb(27, 27, 31);
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(239, 251, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.thin-line {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
li {
  list-style-type: none;
}
.badge-con {
  position: relative;
}
.badge {
  position: absolute;
  height: 20px;
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 50%;
  top: -50%;
  background-color: rgb(0, 132, 255);
  color: white;
  border-radius: 4px;
  font-size: 0.8rem;
}
.phn-badge-con {
  position: relative;
}
.phn-badge {
  position: absolute;
  height: 20px;
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  top: calc(50% - 10px);
  background-color: rgb(0, 132, 255);
  color: white;
  border-radius: 4px;
  font-size: 0.8rem;
}

.react-multiple-carousel__arrow--right {
  z-index: 1 !important;
}
