.header-con {
  background-color: white;
  /* border-bottom: 1px solid rgba(0, 0, 0, 0.1); */
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  height: 60px;
  margin: 0 auto;
}

.search {
  /* margin: 0 24px; */
  padding: 0 12px;
  border-radius: 8px;
  height: 30px;
  width: 160px;
  border: 2px solid rgba(0, 0, 0, 0.05);
  background-color: rgba(0, 0, 0, 0.05);
}
.search:focus {
  outline: none;
  background-color: rgba(0, 0, 0, 0.1);
}
menu li {
  display: inline-block;
  margin: 0 8px;
}
.main-menu {
}
.phn-menu-btn {
  display: none;
}
@media (max-width: 600px) {
  .main-menu {
    display: none;
  }
  .phn-menu-btn {
    display: block;
  }
}
.phn-menu-con {
  /* width: 90vh; */
  /* z-index: 1; */
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
}
.phn-menu {
  width: fit-content;
  padding: 1rem 2rem;
  height: 100vh;
  background-color: white;
  margin-left: auto;
}

.phn-menu li {
  /* background-color: blue; */
  margin: 1rem 0;
}
